'use client';
import React from 'react';

export default function PriorityMatrix({ issues = [] }) {
  const safe = Array.isArray(issues) ? issues : [];

  // Tomtillst√•nd ‚Äì viktig f√∂r att undvika att sidan ser "trasig" ut n√§r det inte finns data
  if (safe.length === 0) {
    return (
      <div
        data-testid="lh-priority-matrix"
        style={{
          marginTop: 16, padding: 16, borderRadius: 12,
          border: '1px solid rgba(0,0,0,0.08)', background: '#fff'
        }}
      >
        <div style={{ fontWeight: 600, marginBottom: 8 }}>Prioriteringsmatris (beta)</div>
        <div style={{ color: '#475569' }}>Inga √•tg√§rder hittades f√∂r denna analys.</div>
      </div>
    );
  }

  // Minimal rendering ‚Äì lista titlar om de finns, utan att anta dataschema
  return (
    <div
      data-testid="lh-priority-matrix"
      style={{
        marginTop: 16, padding: 16, borderRadius: 12,
        border: '1px solid rgba(0,0,0,0.08)', background: '#fff'
      }}
    >
      <div style={{ fontWeight: 600, marginBottom: 8 }}>Prioriteringsmatris (beta)</div>
      <ul style={{ margin: 0, paddingLeft: 18 }}>
        {safe.slice(0, 20).map((it, idx) => (
          <li key={idx} style={{ margin: '4px 0' }}>
            {String(it?.title || it?.description || it?.id || '√Ötg√§rd')}
          </li>
        ))}
      </ul>
    </div>
  );
}