import React from 'react';
import './SEOHealthGauge.css';

// CSS-based SEO Health Gauge without external dependencies
function SEOHealthGauge({ score }) {
  const getScoreColor = (score) => {
    if (score >= 75) return '#16a34a'; // Green
    if (score >= 50) return '#f59e0b'; // Yellow/Orange
    return '#ef4444'; // Red
  };

  const getScoreLabel = (score) => {
    if (score >= 75) return 'Bra';
    if (score >= 50) return 'OK';
    return 'Beh√∂ver f√∂rb√§ttras';
  };

  const getScoreEmoji = (score) => {
    if (score >= 75) return 'üü¢';
    if (score >= 50) return 'üü°';
    return 'üî¥';
  };

  // Calculate percentage for CSS circle
  const percentage = Math.min(100, Math.max(0, score));
  
  return (
    <div className="seo-health-gauge">
      <div className="gauge-header">
        <h3>SEO Health</h3>
      </div>
      
      <div className="gauge-container">
        <div className="gauge-chart">
          <div className="circular-gauge">
            {/* CSS-based circular progress bar */}
            <svg width="200" height="200" viewBox="0 0 200 200">
              {/* Background circle */}
              <circle
                cx="100"
                cy="100"
                r="80"
                fill="none"
                stroke="#e5e5e5"
                strokeWidth="20"
              />
              {/* Progress circle */}
              <circle
                cx="100"
                cy="100"
                r="80"
                fill="none"
                stroke={getScoreColor(score)}
                strokeWidth="20"
                strokeLinecap="round"
                strokeDasharray={`${2 * Math.PI * 80}`}
                strokeDashoffset={`${2 * Math.PI * 80 * (1 - percentage / 100)}`}
                transform="rotate(-90 100 100)"
              />
            </svg>
            
            {/* Center score display */}
            <div className="gauge-center">
              <div className="score-value">{score}</div>
            </div>
          </div>
        </div>
        
        <div className="gauge-footer">
          <div className={`score-status ${score >= 75 ? 'good' : score >= 50 ? 'ok' : 'poor'}`}>
            <span className="status-emoji">{getScoreEmoji(score)}</span>
            <span className="status-text">{getScoreLabel(score)}</span>
          </div>
        </div>
      </div>
      
      <style jsx>{`
        .seo-health-gauge {
          background: white;
          border-radius: 12px;
          padding: 20px;
          box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
          text-align: center;
          min-height: 300px;
        }
        
        .gauge-header h3 {
          margin: 0 0 20px 0;
          font-size: 18px;
          font-weight: 600;
          color: #374151;
        }
        
        .gauge-chart {
          position: relative;
          display: inline-block;
        }
        
        .circular-gauge {
          position: relative;
        }
        
        .gauge-center {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          text-align: center;
        }
        
        .score-value {
          font-size: 36px;
          font-weight: bold;
          color: ${getScoreColor(score)};
          line-height: 1;
        }
        
        .score-max {
          font-size: 14px;
          color: #6b7280;
          margin-top: -5px;
        }
        
        .gauge-footer {
          margin-top: 15px;
        }
        
        .score-status {
          display: flex;
          align-items: center;
          justify-content: center;
          gap: 8px;
          font-size: 16px;
          font-weight: 500;
        }
        
        .score-status.good {
          color: #16a34a;
        }
        
        .score-status.ok {
          color: #f59e0b;
        }
        
        .score-status.poor {
          color: #ef4444;
        }
        
        .status-emoji {
          font-size: 18px;
        }
      `}</style>
    </div>
  );
}

export default SEOHealthGauge;